/* BattleScene.css */
.battle-scene {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: none;
    background-color: black;
    z-index: 999;
}

.battle-scene.show {
    display: block;
}

.profile-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
}

.vs-text {
    font-size: 126px;
    color: rgb(247, 242, 183);
    text-shadow: 1px 1px 3px #d0d407;
    font-weight: bold;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    -webkit-text-stroke: 4px black;
    z-index: 4;
    opacity: 0;
    animation: vsAnimation 1.5s forwards 2s;
}

.background {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 50%;
    background-size: cover;
    z-index: -2;
}

.background-left {
    left: 0;
    background-image: url('../images/profile-background.webp');
    transform: scale(-1);
    animation: backgroundLeftAnimation 5s forwards;
}

.background-right {
    right: 0;
    background-image: url('../images/profile-background.webp');
    animation: backgroundRightAnimation 5s forwards;
}

@keyframes vsAnimation {
    0% { opacity: 0; transform: translateX(-50%) scale(0); }
    50% { opacity: 1; transform: translateX(-50%) scale(1.2); }
    100% { opacity: 1; transform: translateX(-50%) scale(1); }
}

@keyframes backgroundLeftAnimation {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(0); }
}

@keyframes backgroundRightAnimation {
    0% { transform: translateX(100%); }
    100% { transform: translateX(0); }
}
